import { useEffect, useState } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import UserModel from "../../../Models/UserModel";
import { authStore } from "../../../Redux/AuthState";
import Login from "../../AuthMenu/Login/Login";
import Logout from "../../AuthMenu/Logout/Logout";
import Register from "../../AuthMenu/Register/Register";
import AddVacation from "../../VacationsArea/AddVacation/AddVacation";
import EditVacation from "../../VacationsArea/EditVacation/EditVacation";
import Vacations from "../../VacationsArea/Vacations/Vacations";
import VacationsStats from "../../VacationsArea/VacationsStats/VacationsStats";
import PageNotFound from "../PageNotFound/PageNotFound";
import "./Routing.css";

function Routing(): JSX.Element {

    const [user, setUser] = useState<UserModel>();

    useEffect(() => {

        setUser(authStore.getState().user);

        const unsubscribe = authStore.subscribe(() => {
            setUser(authStore.getState().user);
        });

        return () => {
            unsubscribe();
        };

    }, []);

    return (
        <div className="Routing">
            <Routes>
                <Route path="/home" element={user? <Navigate to="/vacations"/>: <Login />} />
                <Route path="/register" element={user? <Navigate to="/vacations"/>: <Register />} />
                <Route path="/logout" element={<Logout />} />

                {user &&
                <>
                <Route path="/vacations" element={<Vacations />} />
                <Route path="/add-vacation" element={<AddVacation />} />
                <Route path="graphs" element={<VacationsStats />} />
                <Route path="/vacation/edit/:vacationId" element={<EditVacation />} />
                </>
                }
                <Route path="/" element={<Navigate to="/home" />} />
                <Route path="*" element={<PageNotFound />} />
            </Routes>
        </div>
    );
}

export default Routing;
