import {  useEffect, useState } from "react"

import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

import { Bar } from 'react-chartjs-2';
import vacationService from "../../../Services/VacationsService";
import "./VacationsStats.css"
import { useNavigate } from "react-router-dom";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
)


export default function VacationsStats():JSX.Element {
    const navigate = useNavigate()
    const [vacationsLabels, setVacationsLabels] = useState<string[]>([])
    const [vacationsFollowersCount, setVacationsFollowersCount] = useState<number[]>([])

    useEffect(() => {
        (async () => {
            const result = await vacationService.getVacationsStats()
            setVacationsLabels(result.map((v) => v.destination))
            setVacationsFollowersCount(result.map((v) => +v.followersCount))
        })()
    }, [])

    if (vacationsLabels.length === 0 || vacationsFollowersCount.length === 0) {
        return <div>
            <h2 style={{ marginTop: "2rem" }}>No Results</h2>
            <button className="back-vacation-btn" onClick={goBack}>Back</button>
        </div>
    }

    const data = {
        labels: vacationsLabels,
        datasets: [
            {
                label: 'Followers',
                data: vacationsFollowersCount,
                borderColor: 'rgb(53, 162, 235)',
                backgroundColor: 'rgba(53, 162, 235, 0.3)',
                borderWidth: 3,
            },
        ],
    }

    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top' as const,
            },
            title: {
                display: true,
                text: 'Vacations Following Stats',
            },
        },
        scales: {
            y: {
                min: 0,
                max: vacationsFollowersCount[vacationsFollowersCount.length - 1] * 8,
                title: {
                    display: true,
                    text: "Number Of Followers",
                    color: "red",
                }
            },
            x: {
                title: {
                    display: true,
                    text: "Vacations",
                    color: "red",
                }
            }

        },
    };


    function goBack() {

        navigate("/vacations")
    }



    return (
        <>
            <>
                <div className="VacationsStats"><Bar options={options} data={data} /></div>
            </>
            <>
                <button className="back-vacation-btn" onClick={goBack}>Back</button>
            </>
        </>






    )
}