import axios from "axios";
import { AuthAction, AuthActionType, authStore } from "../Redux/AuthState";

class InterceptorService {

    public createInterceptor() {


        axios.interceptors.request.use(request => {

    
            if (authStore.getState().token) {

           
                request.headers = {
                    "authorization": "Bearer " + authStore.getState().token // Must be that format
                };
            }

       
            return request;

        });


        axios.interceptors.response.use((response) => {
            return response
        }, (error) => {

            console.log(error)



            if (error.response.data === "You are not logged in" || error.response.status === 500) {
                const action: AuthAction = { type: AuthActionType.LoggedInFail, payload: error.response.data };
                authStore.dispatch(action);
            }
         
            return Promise.reject(error)
        });
    }
}

const interceptorService = new InterceptorService();

export { interceptorService }