import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { NavLink } from "react-router-dom";
import { useNavigate, useParams } from "react-router-dom";
import VacationModel from "../../../Models/VacationModel";
import { vacationsStore } from "../../../Redux/VacationsState";
import notifyService from "../../../Services/NotifyService";
import vacationService from "../../../Services/VacationsService";

import "./EditVacation.css";

function EditVacation(): JSX.Element {

    const params = useParams();
    const navigate = useNavigate();
    const { register, handleSubmit, formState, setValue, getValues } = useForm<VacationModel>();

    const [departDateInFocus, setDepartDateInFocus] = useState<boolean>()
    const [returnDateInFocus, setReturnDateInFocus] = useState<boolean>()

    const [currentVacation, setCurrentVacation] = useState<VacationModel>()

    const vacations = vacationsStore.getState().vacations


    useEffect(() => {
        const id = +params.vacationId;
        const vacation = vacations.find((v:VacationModel) => v.vacationId === id)
console.log(vacation)
        if (!vacation) {
            navigate("/vacations")

        } else {

            setValue("destination", vacation.destination);
            setValue("description", vacation.description);
            setValue("departDate", (vacation.departDate).slice(0, 10).split("-").reverse().join("-"));
            setValue("returnDate", (vacation.returnDate).slice(0, 10).split("-").reverse().join("-"));
            setValue("price", vacation.price);
            setValue("image", vacation.image);
            setValue("imageName", vacation.imageName);
            setValue("vacationId", vacation.vacationId);


            setCurrentVacation(vacation)

        }
    }, []);

    async function send(vacation: VacationModel) {

        const departDate = new Date(vacation.departDate).getTime()
        const returnDate = new Date(vacation.returnDate).getTime()
        const today = new Date().getTime()

        if (departDate >= returnDate) {
            alert("Vacation depart date cannot be in the past!")
            return
        }
        if (departDate <= today) {

            alert("Vacation depart date cannot be in the past!")
            return
        }


        try {
            await vacationService.updateVacation(vacation);

            notifyService.success("Vacation has been updated");

            navigate("/vacations");
        }
        catch (err: any) {
            notifyService.error(err);
        }
    }

    function cancelUpdate() {

        const iAmSure = window.confirm("Are you sure you want to cancel?");

        if (!iAmSure) return;

        navigate("/vacations");
    }

    return (
        <div className="EditVacation">

            <form onSubmit={handleSubmit(send)}>

                <h2>Edit Vacation</h2>

                <div className="inputBox">


                    <input type="text" required {...register("destination", {
                        required: { value: true, message: "Missing destination name" },
                        minLength: { value: 2, message: "Destination too short" },
                        maxLength: { value: 100, message: "Destination too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.destination?.message}</span>
                    <span className="label">Destination </span>

                </div>

                <div className="inputBox">

                    <input type="text" required {...register("description", {
                        required: { value: true, message: "Missing description" },
                        minLength: { value: 2, message: "Description too short" },
                        maxLength: { value: 250, message: "Description too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.description?.message}</span>
                    <span className="label">Description</span>

                </div>

                <div className="inputBox" onBlur={() => setDepartDateInFocus(false)}>


                    <input onFocus={() => setDepartDateInFocus(true)} type={departDateInFocus ? "date" : ""} className="date-box" required {...register("departDate", {
                        required: { value: true, message: "Missing departDate" },
                    })} />

                    <span className="span-validation-error">{formState.errors.departDate?.message}</span>
                    <span className="label">Depart Date</span>

                </div>

                <div className="inputBox" onBlur={() => setReturnDateInFocus(false)}>


                    <input onFocus={() => setReturnDateInFocus(true)} type={returnDateInFocus ? "date" : ""} className="date-box" required {...register("returnDate", {
                        required: { value: true, message: "Missing returnDate" },
                    })} />

                    <span className="span-validation-error">{formState.errors.returnDate?.message}</span>
                    <span className="label">Return Date</span>

                </div>

                <div className="inputBox">

                    <input type="number" required {...register("price", {
                        required: { value: true, message: "Missing price" },
                        min: { value: 0, message: "Price can't be negative" },
                        max: { value: 5000, message: "Price can't exceed 5000" },
                    })} />
                    <span className="span-validation-error">{formState.errors.price?.message}</span>
                    <span className="label">Price </span>

                </div>

                <div className="inputBox">

                    <input type="file"  accept="image/*" {...register("image",{
                        // required:{value:true, message:"Missing Image"}
                    })} />

                    {currentVacation &&
                        <img src={"http://localhost:3007/1-assets/images/" + getValues("imageName")} className="edit-image" />
                    }

                </div>

                <div className="edit-btn-container">

                    <button className="update-vacation-btn">Update</button>

                    <button className="cancel-vacation-btn" type="button" onClick={cancelUpdate}>Cancel</button>

                </div>

            </form>

        </div>
    );
}

export default EditVacation;
