import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import CredentialsModel from "../../../Models/CredentialsModel";
import authService from "../../../Services/AuthService";
import notifyService from "../../../Services/NotifyService";
import "./Login.css";

function Login(): JSX.Element {

    const { register, handleSubmit, formState } = useForm<CredentialsModel>();
    const navigate = useNavigate();

    async function send(credentials: CredentialsModel) {
        try {
            
            await authService.login(credentials);
            notifyService.success("Welcome Back!");
            navigate("/vacations/");
        }
        catch (err: any) {
            console.log(err);
            
            notifyService.error(err.response.data);
           
        }
    }

    useEffect(() => {
        authService.loggedIn()
            .then((response) => {
                console.log("response: ",response)
            })
    }, [])

    return (
        <div className="Login">

            <form onSubmit={handleSubmit(send)}>

                  
                <h2>Login</h2>

                <div className="inputBox">
                    <input type="text" required {...register("userName", {
                        required: { value: true, message: "Missing username" },
                    })} />
                    <span className="span-validation-error">{formState.errors.userName?.message}</span>
                    <span className="label">Username </span>
                    

                </div>
                
                <div className="inputBox">

                    <input type="password" required {...register("password", {
                        required: { value: true, message: "Missing password" },
                    })} />
                    <span className="span-validation-error">{formState.errors.password?.message}</span>
                    <span className="label">Password </span>

                </div>

                <button>Login</button>

            </form>

        </div>
    );
}

export default Login;
