import { createStore } from "redux";
import vacationModel from "../Models/VacationModel";


export class VacationsState {
    public vacations: vacationModel[] = [];
}


export enum VacationsActionType {
    FetchVacations = "Fetchvacations", 
    AddVacation = "Addvacation",
    UpdateVacation = "Updatevacation",
    DeleteVacation = "Deletevacation",
    FollowVacation="FollowVacation"
}

export interface VacationsAction {
    type: VacationsActionType; 
    payload: any; 
}


export function vacationsReducer(currentState = new VacationsState(), action: VacationsAction): VacationsState {

    const newState = { ...currentState }; 

 
    switch (action.type) {

        case VacationsActionType.FollowVacation:
           const vacation = newState.vacations.find((v)=> v.vacationId === action.payload.vacationId)

           vacation.isFollowing = action.payload.isFollow === 0 ? 1:0

           console.log(vacation)

        break

        case VacationsActionType.FetchVacations: 
            newState.vacations = action.payload; 
            break;

        case VacationsActionType.AddVacation: 
            newState.vacations.push(action.payload); 
            break;

        case VacationsActionType.UpdateVacation: 
            const indexToUpdate = newState.vacations.findIndex(v => v.vacationId === action.payload.id);
            if (indexToUpdate >= 0) {
                newState.vacations[indexToUpdate] = action.payload;
            }
            break;

        case VacationsActionType.DeleteVacation: 
            const indexToDelete = newState.vacations.findIndex(v => v.vacationId === action.payload);
            if (indexToDelete >= 0) {
                newState.vacations.splice(indexToDelete, 1); 
            }
            break;
    }

    return newState;
}


export const vacationsStore = createStore(vacationsReducer);
