import "./VacationCard.css";
import VacationModel from "../../../Models/VacationModel"
import { useState } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { faCalendarDays } from '@fortawesome/free-solid-svg-icons'
import { faHeart } from '@fortawesome/free-solid-svg-icons'
import { faEdit } from '@fortawesome/free-solid-svg-icons'
import { faTrash } from '@fortawesome/free-solid-svg-icons'
import vacationService from "../../../Services/VacationsService";
import UserModel from "../../../Models/UserModel";
import { useNavigate } from "react-router-dom";
import { NavLink } from "react-router-dom";
import RoleModel from "../../../Models/RoleModel";




interface VacationCardProps {
    vacation: VacationModel;
    user: UserModel;
    deleteVacation:(vacationId:number)=> void
}

function VacationCard(props: VacationCardProps): JSX.Element {

    const [isActive, setIsActive] = useState(false);

    const [isFollowing, setIsFollowing] = useState(props.vacation.isFollowing)

    const [followersCount, setFollowersCount] = useState(props.vacation.followersCount)


    const navigate = useNavigate();

    function showDetails() {

        setIsActive(current => !current)

    }

    async function followUnfollow() {

        console.log("Heart is being clicked!")

        const vacationId = props.vacation.vacationId
            
        await vacationService.updateIsFollowing(vacationId, isFollowing ===1 ? 1:0)
        
        isFollowing === 1 ? setIsFollowing(0) : setIsFollowing(1)

        setFollowersCount((prev)=> isFollowing ===1 ? prev-1:prev+1)

    }

    async function deleteVacation() {

            const iAmSure = window.confirm("Are you sure you want to delete this product?");

            if (!iAmSure) return;

            props.deleteVacation(props.vacation.vacationId)

          
    }

    return (

        <div className="VacationCard" style={{ height: isActive ? "550px" : '' }}>

            {
                props.user.roleName === RoleModel.User &&
                <>
                    <div className="follow-me-bg"><FontAwesomeIcon icon={faHeart}></FontAwesomeIcon></div>

                    <button className="follow-me-outline" onClick={followUnfollow} style={isFollowing === 1 ? { "color": "rgb(136, 28, 28)" } : { "color": "transparent" }}> <FontAwesomeIcon icon={faHeart}></FontAwesomeIcon></button>
                </>

            }

            <div className="followers-counter">Followers: <span>{followersCount}</span></div>

            <div className="image-container">

                <img src={"http://localhost:3007/1-assets/images/" + props.vacation.imageName} />

                {
                    props.user.roleName === RoleModel.Admin&&
                    <>
                        <div className="edit-tools">

                            <NavLink to={"/vacation/edit/" + props.vacation.vacationId} className="edit-btn">
                                <button className="inner-edit-btn"><FontAwesomeIcon icon={faEdit}></FontAwesomeIcon> </button>
                            </NavLink>

                            <button className="delete-btn" onClick={deleteVacation} value={props.vacation.vacationId}><FontAwesomeIcon icon={faTrash}></FontAwesomeIcon></button>

                        </div>
                    </>
                }


                <p className="destination"> {props.vacation.destination} </p>

                <button className="dropdown-menu-btn" onClick={showDetails} value={props.vacation.vacationId}><FontAwesomeIcon style={{ rotate: isActive ? "180deg" : '' }} icon={faChevronDown}></FontAwesomeIcon></button>

            </div>

            <div className="details">

                <p className="description">{props.vacation.description}</p>

                <p className="dates"> <FontAwesomeIcon icon={faCalendarDays}></FontAwesomeIcon> {props.vacation.departDate.slice(0, 10).split("-").reverse().join("-")} <FontAwesomeIcon icon={faArrowRight}></FontAwesomeIcon> {props.vacation.returnDate.slice(0, 10).split("-").reverse().join("-")}</p>

                <p className="price">{props.vacation.price}$</p>

            </div>

        </div>
    );
}

export default VacationCard;
