import axios from "axios"
import VacationModel from "../Models/VacationModel"
import { authStore } from "../Redux/AuthState"
import { VacationsAction, VacationsActionType, vacationsStore } from "../Redux/VacationsState"
import appConfig from "../Utils/Config"

class VacationService {

    public async getAllVacations(): Promise<VacationModel[]> {
        const response = await axios.get<VacationModel[]>(appConfig.apiUrl + "/vacations/", {
            headers: {
                "authorization": "Bearer " + authStore.getState().token
            }
        })


        const action: VacationsAction = { type: VacationsActionType.FetchVacations, payload: response.data };
        vacationsStore.dispatch(action); 
        return response.data
    }


    public async getOneVacation(vacationId: number): Promise<VacationModel> {


        let vacation;

    
        let vacations = vacationsStore.getState().vacations;

  
        if (vacations.length === 0) {

         
            const response = await axios.get<VacationModel>(appConfig.apiUrl + "/vacations/" + vacationId);

        
            vacation = response.data;
        }
        else {


            vacation = vacations.find(v => v.vacationId === vacationId);
        }

 
        return vacation;
    }


    public async updateIsFollowing(vacationId: number, isFollow: number): Promise<void> {

        const response = await axios.post<VacationModel>(appConfig.apiUrl + "/vacations/following",
            { vacationId: vacationId, isFollow: isFollow })

            const action: VacationsAction = { type: VacationsActionType.FollowVacation, payload: {vacationId,isFollow} };
            vacationsStore.dispatch(action); 


        const vacation = response.data

    }

    public async AddVacation(vacation: VacationModel): Promise<void> {

       
        const formData = new FormData();
        formData.append("destination", vacation.destination);
        formData.append("description", vacation.description);
        formData.append("departDate", vacation.departDate);
        formData.append("returnDate", vacation.returnDate);
        formData.append("price", vacation.price.toString());
        formData.append("image", vacation.image[0]);

        
        const response = await axios.post<VacationModel>(appConfig.apiUrl + "/vacations", formData);
        const addedVacation: VacationModel = response.data;

   
        const action: VacationsAction = { type: VacationsActionType.AddVacation, payload: addedVacation };
        vacationsStore.dispatch(action); 
    }

    public async updateVacation(vacation: VacationModel): Promise<void> {

      
        const formData = new FormData();
        formData.append("destination", vacation.destination);
        formData.append("description", vacation.description);
        formData.append("departDate", vacation.departDate);
        formData.append("returnDate", vacation.returnDate);
        formData.append("price", vacation.price.toString());
        formData.append("imageName", vacation.imageName);


        if(vacation?.image){
            formData.append("image", vacation.image[0]);
        }


        const response = await axios.put<VacationModel>(appConfig.apiUrl + "/vacations/" + vacation.vacationId, formData);
        const updatedProduct = response.data;

       
        const action: VacationsAction = { type: VacationsActionType.UpdateVacation, payload: updatedProduct };
        vacationsStore.dispatch(action);
    }




    public async deleteVacation(vacationId: number): Promise<void> {

       
        await axios.delete(appConfig.apiUrl + "/vacations/" + vacationId);


        const action: VacationsAction = { type: VacationsActionType.DeleteVacation, payload: vacationId };
        vacationsStore.dispatch(action);
    }

    public async getVacationsStats(): Promise<VacationModel[]> {
        const response = await axios.get<VacationModel[]>(appConfig.apiUrl + "/vacations/stats")
        return response.data
    }

}

const vacationService = new VacationService()
export default vacationService
