import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import UserModel from "../../../Models/UserModel";
import authService from "../../../Services/AuthService";
import notifyService from "../../../Services/NotifyService";
import "./Register.css";

function Register(): JSX.Element {

    const { register, handleSubmit, formState } = useForm<UserModel>();
    const navigate = useNavigate();

    async function send(user: UserModel) {
        try {
            await authService.register(user);
            notifyService.success("Welcome!");
            navigate("/vacations");
        }
        catch (err: any) {
            notifyService.error(err);
        }
    }

    return (
        <div className="Register Box">

            <form onSubmit={handleSubmit(send)}>

                <h2>Register</h2>

                <div className="inputBox">

           
                    <input type="text" required {...register("firstName", {
                        required: { value: true, message: "Missing first name" },
                        minLength: { value: 2, message: "First name too short" },
                        maxLength: { value: 100, message: "First name too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.firstName?.message}</span>
                    <span className="label">First name </span>

                </div>

                <div className="inputBox">

                    <input type="text" required {...register("lastName", {
                        required: { value: true, message: "Missing last name" },
                        minLength: { value: 2, message: "Last name too short" },
                        maxLength: { value: 100, message: "Last name too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.lastName?.message}</span>
                    <span className="label">Last name </span>

                </div>

                <div className="inputBox">

      
                    <input type="text" required {...register("userName", {
                        required: { value: true, message: "Missing username" },
                        minLength: { value: 4, message: "Username too short" },
                        maxLength: { value: 100, message: "Username too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.userName?.message}</span>
                    <span className="label">Username </span>

                </div>

                <div className="inputBox">

                    <input type="password" required {...register("password", {
                        required: { value: true, message: "Missing password" },
                        minLength: { value: 4, message: "Password too short" },
                        maxLength: { value: 100, message: "Password too long" },
                    })} />
                    <span className="span-validation-error">{formState.errors.password?.message}</span>
                    <span className="label">Password </span>

                </div>

                <button>Register</button>

            </form>

        </div>
    );
}

export default Register;
