import { useEffect, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import RoleModel from "../../../Models/RoleModel";
import VacationModel from "../../../Models/VacationModel";
import { authStore } from "../../../Redux/AuthState";
import { vacationsStore } from "../../../Redux/VacationsState";
import notifyService from "../../../Services/NotifyService";
import vacationService from "../../../Services/VacationsService";
import Pagination from "../../LayoutArea/Pagination/Pagination";
import VacationCard from "../VacationCard/VacationCard";
import "./Vacations.css";

function Vacations(): JSX.Element {

    const [allVacations, setAllVacations] = useState<VacationModel[]>([])
    const [currentPage, setCurrentPage] = useState<number>(1)
    const vacationsPerPage = 10 
    const navigate = useNavigate();

    const user = authStore.getState().user

    const lastVacationIndex = currentPage * vacationsPerPage
    const firstVacationIndex = lastVacationIndex - vacationsPerPage
    const currentVacations = allVacations.slice(firstVacationIndex, lastVacationIndex)

    async function onDeleteVacation(vacationId: number) {
        try {
            await vacationService.deleteVacation(vacationId);

            notifyService.success("Vacation has been deleted")

            setAllVacations(allVacations.filter((v) => v.vacationId !== vacationId))

            navigate("/vacations");
        }
        catch (err: any) {
            notifyService.error(err);
        }
    }


    function onMyVacations() {
        setCurrentPage(1)
        const vacations = vacationsStore.getState().vacations
        const myVacations = vacations.filter((v:VacationModel) => v.isFollowing)
        setAllVacations(myVacations)
    }

    useEffect(() => {
        vacationService.getAllVacations()
            .then(data => setAllVacations(data))

    }, [])

    function showAllVacations() {
        const vacations = vacationsStore.getState().vacations
        setAllVacations(vacations)
    }

    return (
        <div className="Vacations">

            <h1>Our Vacations</h1>

            {user.roleName === RoleModel.Admin &&
                <>
                    <NavLink to="/add-vacation"><button className="add-btn">Add Vacation</button></NavLink>

                    <NavLink to={"/graphs"}><button className="graph-btn">Graphs</button> </NavLink>

                </>
            }

            {user.roleName === RoleModel.User &&
                <>
                    <button className="my-vacations-btn" onClick={onMyVacations} >My Vacations</button>

                    <button className="all-vacations" onClick={showAllVacations} >All Vacations</button>


                </>
            }

            <div className="vacations-container">

                {currentVacations.map(v => <VacationCard
                    key={v.vacationId}
                    deleteVacation={onDeleteVacation}
                    vacation={v}
                    user={user}
                />)}
            </div>


            {

                allVacations.length > 10 &&

                <div>
                    <Pagination totalVacations={allVacations.length} vacationsPerPage={vacationsPerPage} setCurrentPage={setCurrentPage} />
                </div>

            }


        </div>
    );
}

export default Vacations;
